<template>
    <v-container id="template" >
        <v-btn id="print" class="blue white--text" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="ml-4 red white--text" to="/suplierpembelian" link>Back</v-btn>
       
       <template id="template">
           <br><h2 class="text-center">Data Suplier</h2><br>
            <table id="table" >
                <thead>
                    <tr>
                        <th id="th">No</th>
                        <th id="th">No Suplier</th>
                        <th id="th">Nama Suplier</th>
                        <th id="th">Contact Person</th>
                        <th id="th">Alamat</th>
                        <th id="th">Kota</th>
                        <th id="th">Kode Pos</th>
                        <th id="th">Telephone</th>
                        <th id="th">Tanggal Hubungan</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in supliers" :key='item.id'>
                        <td id="td" width="100px" class="text-center"><pre>{{index + 1}}</pre></td>
                        <td id="td" width="100px" class="text-center"><pre>{{item["No Suplier"]}}</pre></td>
                        <td id="td" width="80px" class="text-center"><pre>{{item["Nama Suplier"]}}</pre></td>
                        <td id="td" class="text-center"><pre>{{item["Contact Person"]}}</pre></td>
                        <td id="td" ><pre>{{item["Alamat"]}}</pre></td>
                        <td id="td" width="100px" class="text-center"><pre>{{item["Kota"]}}</pre></td>
                        <td id="td" width="100px" class="text-center"><pre>{{item["Kode Pos"] }}</pre></td>
                        <td id="td" width="120px" class="text-center"><pre>{{item["Telephone"]}}</pre></td>
                        <td id="td" width="150px" class="text-center"><pre>{{item["Tanggal Hubungan"]}}</pre></td>
                    </tr>
                </tbody>
            </table>
       </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                keyword: '',
                load: false,
                supliers: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'suplierpembelians'
                this.$http.get(uri).then(response => {
                    this.supliers = response.data.data
                })
            },

            
            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style>
    @media print{
        #print{
            display: none;
        }
        #template{
            background-color: white
        }
    }
    
    #table, #th, #td {
        border: 2px solid black;
        border-collapse: collapse;
    }
</style>